import {ConfigModel, ConfigObject, DirectionMarginOrPadding} from "@/models/configModel";
import {getSize, getStyle, getStyleMarginPadding, setStyle, transformVarToColor} from "@/models/ConfigMethod";
import {Component, Model, Vue, Watch} from "vue-property-decorator";
import {STORAGE_CONFIG} from "../../Constants";

@Component
export class ConfigGeneral extends Vue implements ConfigModel {


    @Model('change', {
        type: Object, default: () => {
            getSize('h1size', '.reveal h1', 'font-size')
        }
    })
    h1size!: ConfigObject<string>;
    @Model('change', {
        type: Object, default: () => {
            getStyle('h1bgc', '.reveal h1', 'background-color')
        }
    })
    h1bgc!: ConfigObject<string>;

    @Model('change', {
        type: Object, default: () => {
            getStyle('h1color', '.reveal h1', 'color')
        }
    })
    h1color!: ConfigObject<string>;
    // h2padding!: string;

    // @Model('change', {type: String, default: getStyle('.reveal h1', 'margin')})
    // h1margin!: string;
    @Model('change', {
        type: Object, default: () => {
            getStyleMarginPadding('h1padding', '.reveal h1', 'padding')
        },
    })
    h1padding: ConfigObject<DirectionMarginOrPadding> = {
        field: 'h1padding',
        selector: '.reveal h1',
        value: {
            top: getStyleMarginPadding('h1padding', '.reveal h1', 'padding-top').value.top,
            right: getStyleMarginPadding('h1padding', '.reveal h1', 'padding-right').value.right,
            bottom: getStyleMarginPadding('h1padding', '.reveal h1', 'padding-bottom').value.bottom,
            left: getStyleMarginPadding('h1padding', '.reveal h1', 'padding-left').value.left,
        }
    };
    @Model('change', {
        type: Object, default: () => {
            getStyleMarginPadding('h1margin', '.reveal h1', 'margin')
        },
    })
    h1margin: ConfigObject<DirectionMarginOrPadding> = {
        field: 'h1margin',
        selector: '.reveal h1',
        value: {
            top: getStyleMarginPadding('h1margin', '.reveal h1', 'margin-top').value.top,
            right: getStyleMarginPadding('h1margin', '.reveal h1', 'margin-right').value.right,
            bottom: getStyleMarginPadding('h1margin', '.reveal h1', 'margin-bottom').value.bottom,
            left: getStyleMarginPadding('h1margin', '.reveal h1', 'margin-left').value.left,
        }
    };

    // End H1

    // Start H2
    @Model('input', {
        type: Object, default: () => {
            getSize('h2size', '.reveal h2', 'font-size')
        }
    })
    h2size!: ConfigObject<string>;
    // h3padding!: string;
    @Model('change', {
        type: Object, default: () => {
            getSize('', '.reveal h3', 'font-size')
        }
    })
    h3size!: ConfigObject<string>;
    // h4padding!: string;
    @Model('change', {
        type: Object, default: () => {
            getSize('h4size', '.reveal h4', 'font-size')
        }
    })
    h4size!: ConfigObject<string>;

    // h2bgc!: string;
    // h2color!: string;
    // h2margin!: string;
    // h5padding!: string;
    @Model('change', {
        type: Object, default: () => {
            getSize('h5size', '.reveal h5', 'font-size')
        }
    })
    h5size!: ConfigObject<string>;
    // h3bgc!: string;
    // h3color!: string;
    // h3margin!: string;
    // h6padding!: string;
    @Model('change', {
        type: Object, default: () => {
            getSize('h6size', '.reveal h6', 'font-size')
        }
    })
    h6size!: ConfigObject<string>;
    // h4bgc!: string;
    // h4color!: string;
    // h4margin!: string;
    // olpadding!: string;
    @Model('change', {
        type: Object, default: () => {
            getSize('psize', '.reveal p', 'font-size')
        }
    })
    psize!: ConfigObject<string>;
    // h5bgc!: string;
    // h5color!: string;
    // h5margin!: string;


    // h6bgc!: string;
    // h6color!: string;
    // h6margin!: string;


    // liafter!: string;
    // libefore!: string;
    // libgc!: string;
    // limargin!: string;
    // lipadding!: string;
    // olbgc!: string;
    // olmargin!: string;

    // pbgc!: string;
    // pcolor!: string;
    // plineheight!: string;
    // pmargin!: string;
    // ppadding!: string;
    // ulbgc!: string;
    // ulmargin!: string;
    // ulpadding!: string;
    // divColumn!: string;
    // divColumnbgc!: string;
    // divColumncolor!: string;
    // divColumnmargin!: string;
    // divColumnpadding!: string;

    @Watch('h1size', {deep: true})
    onH1Size(val: ConfigObject<string>) {
        setStyle('.reveal h1', 'font-size', val.value, "em");
        localStorage.setItem(STORAGE_CONFIG.GENERAL, this.toJson());
    }

    @Watch('h1bgc', {deep: true})
    onH1Bgc(val: ConfigObject<string>) {
        this.setColor(val, '.reveal h1', 'background-color', this.h1bgc);
    }

    @Watch('h1color', {deep: true})
    onH1Color(val: ConfigObject<string>) {
        this.setColor(val, '.reveal h1', 'color', this.h1color);
    }

    @Watch('h1padding', {deep: true})
    onH1Padding(val: ConfigObject<DirectionMarginOrPadding>) {
        setStyle('.reveal h1', 'padding-top', val.value.top, 'em');
        setStyle('.reveal h1', 'padding-right', val.value.right, 'em');
        setStyle('.reveal h1', 'padding-bottom', val.value.bottom, 'em');
        setStyle('.reveal h1', 'padding-left', val.value.left, 'em');
        localStorage.setItem(STORAGE_CONFIG.GENERAL, this.toJson());
    }

    @Watch('h1margin', {deep: true})
    onH1Margin(val: ConfigObject<DirectionMarginOrPadding>) {
        setStyle('.reveal h1', 'margin-top', val.value.top, 'em');
        setStyle('.reveal h1', 'margin-right', val.value.right, 'em');
        setStyle('.reveal h1', 'margin-bottom', val.value.bottom, 'em');
        setStyle('.reveal h1', 'margin-left', val.value.left, 'em');
        localStorage.setItem(STORAGE_CONFIG.GENERAL, this.toJson());
    }


    setColor(val: ConfigObject<string>, selector: string, propertyCss: string, field: ConfigObject<string>) {
        if (val.value == 'rgba(0, 0, 0, 0)') {
            field.value = 'rgba(255, 255, 255, 1)';
            setStyle(selector, propertyCss, field.value);
            localStorage.setItem(STORAGE_CONFIG.GENERAL, this.toJson());
            return;
        }
        if (val['rgba'] != undefined) {
            setStyle(selector, propertyCss, `rgba(${val["rgba"]['r']}, ${val["rgba"]['g']}, ${val["rgba"]['b']}, ${val["rgba"]['a']})`);
        } else if (val.value.replace(/\s/g, "").match(/rgba\(\d+,\d+,\d+,\d+\)/gi) || val.value != 'rgba(0, 0, 0, 0)') {
            setStyle(selector, propertyCss, transformVarToColor(val.value));
            field = val;
        }
        localStorage.setItem(STORAGE_CONFIG.GENERAL, this.toJson());
    }


    toJson() {
        return JSON.stringify({
            h1size: this.h1size,
            h1bgc: this.h1bgc,
            h1color: this.h1color,
            h2size: this.h2size,
            h3size: this.h3size,
            h4size: this.h4size,
            h5size: this.h5size,
            h6size: this.h6size,
            psize: this.psize,
            h1padding: this.h1padding,
            h1margin: this.h1margin,
        });
    }

    mapToObject(json: string) {
        this.h1size = json['h1size'];
        this.h1bgc = json['h1bgc'];
        this.h1color = json['h1color'];
        this.h2size = json['h2size'];
        this.h3size = json['h3size'];
        this.h4size = json['h4size'];
        this.h5size = json['h5size'];
        this.h6size = json['h6size'];
        this.psize = json['psize'];
        this.h1padding = json['h1padding'];
        this.h1margin = json['h1margin'];
    }

    charging() {
        this.h1size = getSize('h1size', '.reveal h1', 'font-size');
        this.h1bgc = getStyle('h1bgc', '.reveal h1', 'background-color');
        this.h1color = getStyle('h1color', '.reveal h1', 'color');
        this.h2size = getSize('h2size', '.reveal h2', 'font-size');
        this.h3size = getSize('h3size', '.reveal h3', 'font-size');
        this.h4size = getSize('h4size', '.reveal h4', 'font-size');
        this.h5size = getSize('h5size', '.reveal h5', 'font-size');
        this.h6size = getSize('h6size', '.reveal h6', 'font-size');
        this.psize = getSize('psize', '.reveal p', 'font-size');
        this.h1padding = getStyleMarginPadding('h1padding', '.reveal h1', 'padding');
        this.h1margin = getStyleMarginPadding('h1margin', '.reveal h1', 'margin');
    }
}
