

















































































































import {Component, Prop, Vue, Watch} from "vue-property-decorator"
import {ConfigGeneral} from "@/models/ConfigGeneral";
import {Chrome} from 'vue-color';
import SlideUpDown from 'vue-slide-up-down'
import ConfigColor from '@/models/ConfigColor'
import {STORAGE_CONFIG} from "../../Constants";
import {ConfigObject} from "@/models/configModel";

@Component({components: {'chrome-picker': Chrome, SlideUpDown: SlideUpDown}})
export default class ConfigGeneralComponent extends Vue {
    @Prop() configGeneral!: ConfigGeneral;
    activeColor = false;
    activeGeneral = true;
    configColor: ConfigColor = new ConfigColor();
    changecolor = false;
    count = 0;

    @Watch('configColor', {deep: true})
    onChangeColor(val: string, oldVal: string) {
        localStorage.setItem(STORAGE_CONFIG.COLOR, JSON.stringify(val));
        if (this.count >= 1) {
            this.changecolor = true;
        }
        this.count++;
        // TODO: if watch change alors mettre à jour toutes les autres couleur des config general
    }

    reloadPage() {
        this.changecolor = false;
        this.count = 0;
        window.location.reload()
    }

    toogleActiveColor() {
        this.activeColor = !this.activeColor
    }

    toogleActiveGeneral() {
        this.activeGeneral = !this.activeGeneral
    }

    checkifConfigColorExistInStorage() {
        const colors = localStorage.getItem(STORAGE_CONFIG.COLOR);
        if (colors != null || colors != undefined) {
            this.configColor = JSON.parse(colors);
        } else {
            localStorage.setItem(STORAGE_CONFIG.COLOR, JSON.stringify(new ConfigColor()));
        }
    }

    colorReturn(field: string) {
        let cc = localStorage.getItem('configColor');
        if (cc != null) {
            cc = JSON.parse(cc);
            if (cc === null) return "";
            if (cc[field] === undefined) return '';
            if (cc[field]['rgba'] === undefined) return cc[field];
            return `rgba(${cc[field]['rgba']['r']},${cc[field]['rgba']['g']},${cc[field]['rgba']['b']},${cc[field]['rgba']['a']})`;
        }
        return null;
    }

    choiceColor(property: string, fieldColor: string) {
        const c = this.configGeneral[property] as ConfigObject<string>;
        c.value = '$' + fieldColor;
        this.configGeneral[property] = c;
        // const config = this.configGeneral[property] as ConfigObject<string>;
        // setColor(config.selector, config.field.includes('bgc') ? 'background-color' : 'color', transformVarToColor(config.value));
        // console.log(config.selector, config.field.includes('bgc') ? 'background-color' : 'color', transformVarToColor(config.value), config.value);
        // localStorage.setItem(STORAGE_CONFIG.GENERAL, this.configGeneral.toJson());
    }

    mounted() {
        this.checkifConfigColorExistInStorage();
        const gg = localStorage.getItem(STORAGE_CONFIG.GENERAL);
        if (gg) {
            this.configGeneral.mapToObject(JSON.parse(gg));
        }
    }
}

