import ConfigColor from "@/models/ConfigColor";
import {STORAGE_CONFIG} from "../../Constants";
import {ConfigModel, ConfigObject, DirectionMarginOrPadding} from "@/models/configModel";

let revealFontSize = 0;


function splitNumber(value: string): string {
    if (value != null) {
        const m = value.match(/([0-9.]{1,6})/gm);
        if (m != null && m.length > 0) {
            return (+m[0]).toFixed(2);
        }
    }
    return value
}

export function transformVarToColor(variable: string): string {
    let storage = localStorage.getItem(STORAGE_CONFIG.COLOR);
    if (storage != null) {
        storage = JSON.parse(storage);
        if (storage != null) {
            storage = storage[variable.substring(1)];
            if (storage != null) {
                if (storage['rgba'] != null) {
                    storage = storage['rgba'];
                    if (storage != null) {
                        return `rgba(${storage['r']}, ${storage['g']}, ${storage['b']}, ${storage['a']})`;
                    }
                } else {
                    return storage;
                }
            }
        }
    }
    return 'inherit'
}

function transformColorToVar(color) {
    const configColor = new ConfigColor();
    for (const c of Object.getOwnPropertyNames(configColor)) {
        let storage = localStorage.getItem(STORAGE_CONFIG.COLOR);
        if (storage != null) {
            storage = JSON.parse(storage);
            if (storage != null) {
                storage = storage[c];
                if (storage != null) {
                    storage = storage['rgba'];
                    if (storage != null) {
                        storage = `rgba(${storage['r']}, ${storage['g']}, ${storage['b']}, ${storage['a']})`;
                        if (storage == color) {
                            return '$' + c;
                        }
                    }
                }
            }
        }
    }
}

export function getStyle(field: string, selector: string, style: string): ConfigObject<string> {
    const storage = localStorage.getItem(STORAGE_CONFIG.GENERAL);
    if (storage != null) {
        const sto = JSON.parse(storage) as ConfigModel;
        for (const [, value] of Object.entries(sto)) {
            if (value.selector === selector && value.field === field) {
                return {field: field, selector: value.selector, value: value.value}
            }
        }
    }
    const sel = document.querySelectorAll(selector);
    if (sel.length > 0) {
        if (style.match(/color/gi)) {
            const s = window.getComputedStyle(sel[0], null).getPropertyValue(style);
            transformColorToVar(s);
        }
        return {
            field: field,
            selector: selector,
            value: window.getComputedStyle(sel[0], null).getPropertyValue(style)
        }
    }
    return {field: "", selector: "", value: ""}
}

export function getStyleMarginPadding(field: string, selector: string, style: string): ConfigObject<DirectionMarginOrPadding> {
    const storage = localStorage.getItem(STORAGE_CONFIG.GENERAL);
    if (storage != null) {
        const sto = JSON.parse(storage) as ConfigModel;
        for (const [, value] of Object.entries(sto)) {
            if (value.selector === selector && value.field === field) {
                return {field: field, selector: value.selector, value: value.value}
            }
        }
    }
    const sel = document.querySelectorAll(selector);
    if (sel.length > 0) {
        if (style.match(/color/gi)) {
            const s = window.getComputedStyle(sel[0], null).getPropertyValue(style);
            transformColorToVar(s);
        }
        console.log(window.getComputedStyle(sel[0], null).getPropertyValue(style));
        return {
            field: field,
            selector: selector,
            value: {top: '0', left: '0', bottom: '0', right: '0'}
        }
    }
    return {field: "", selector: "", value: {top: '0', left: '0', right: '0', bottom: '0'}}
}


function pxToEm(px: number, field: string): string {
    if (revealFontSize == 0) {
        revealFontSize = +splitNumber(getStyle(field, '.reveal', 'font-size').value);
        // TODO: Si supérieur à 8 alors c'est presque sur que ce soit un px et non em sinon ça divise déjà le em donc problème.
        if (px > 8) {
            return (px / +revealFontSize).toFixed(2)
        }
    }
    return px.toFixed(2)
}

export function setStyle(selector: string, property: string, value: string, supplement = "") {
    const doc = document.querySelectorAll(selector);
    for (const val of doc) {
        (val as HTMLElement).style.setProperty(property, value + supplement);
    }
}

export function getSize(field: string, selector: string, property: string): ConfigObject<string> {
    return {
        field: field,
        selector: selector,
        value: pxToEm(+splitNumber(getStyle(field, selector, property).value), field)
    }
}
