












import {Component, Vue} from "vue-property-decorator";
import {AllSlides} from "@/models/AllSlides"
import {SlideModel} from "@/models/slideModel";
import Slide from "@/components/Slide.vue";
import ConfigComponent from "@/components/ConfigComponent.vue";

@Component({components: {Slide, ConfigComponent}})
export default class IndexComponent extends Vue {
    allSlides: Array<SlideModel> = new Array<SlideModel>();

    beforeMount() {
        // localStorage.clear()
        const l = localStorage.getItem("allSlides");
        const storage = l != null ? JSON.parse(l) : null;
        if (storage === null || storage === undefined) {
            this.allSlides = new AllSlides().allSlides;
            localStorage.setItem("allSlides", JSON.stringify(this.allSlides));
        } else {
            this.allSlides = storage;
        }
    }
}
