import Vue from "vue";
import App from "./App.vue";
import {library} from '@fortawesome/fontawesome-svg-core'
import {faSpinner, faSyncAlt, faUserSecret} from '@fortawesome/free-solid-svg-icons'
import {FontAwesomeIcon} from '@fortawesome/vue-fontawesome'

library.add(faUserSecret, faSpinner, faSyncAlt);

Vue.component('font-awesome-icon', FontAwesomeIcon);

Vue.config.productionTip = false;

Vue.filter("removeDash", function (val: string) {
  return val.replace('-', " ")
});

new Vue({
  render: h => h(App)
}).$mount("#app");


