


















import {Component, Prop, Vue, Watch} from "vue-property-decorator"
import {SlideModel} from "@/models/slideModel"

@Component
export default class Slide extends Vue {
    @Prop() slide!: SlideModel;
    displayTextarea = "none";
    displayHtml = "block";
    btnName = 'text';

    htmlChange() {
        if (this.displayTextarea === 'block') {
            this.displayTextarea = 'none';
            this.displayHtml = 'block';
            this.btnName = 'Text';
        } else {
            this.displayTextarea = 'block';
            this.displayHtml = 'none';
            this.btnName = 'Html';
        }
    }

    ifChangeHtml(content: string) {
        const l = localStorage.getItem('allSlides');
        if (l != null) {
            const storage = JSON.parse(l) as Array<SlideModel>;
            for (let i = 0; i < storage.length; i++) {
                if (storage[i].name === this.slide.name) {
                    storage[i].content = content;
                    localStorage.setItem('allSlides', JSON.stringify(storage))
                }
            }
        }
    }

    getClassNameForJson(value: string) {
        const doc = new DOMParser().parseFromString(value, 'text/html');
        for (const val of doc.querySelectorAll('section.container')) {

            if (val.className.includes('default')) {
                return 'Default'
            } else if (val.className.includes('title')) {
                return 'Title'
            } else if (val.className.includes('headerAndColumns')) {
                return 'HeaderAndColumns'
            } else if (val.className.includes('sectiontitle')) {
                return 'SectionTitle'
            } else if (val.className.includes('split')) {
                return 'Split'
            } else if (val.className.includes('keypoint')) {
                return 'KeyPoint'
            } else if (val.className.includes('titleandcolumns')) {
                return 'HeaderAndColumns'
            }
        }
    }

    @Watch('slide.content')
    slideContentWatch(html: string) {
        this.ifChangeHtml(html)
    }

    beforeMount() {
        this.ifChangeHtml(this.slide.content)
    }
}
